#castbar{position:fixed;cursor:default;left:0;bottom:0;z-index:1030;width:100%;background-color:#000;padding-top:10px;padding-bottom:10px;border-top:1px solid #ccc}#castbar .text{font-size:16px;text-align:center}#castbar a{color:#fff;cursor:pointer}#castbar a:hover{text-decoration:none}#castbar .controls{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#castbar .controls .control{font-size:30px;cursor:pointer;min-width:50px}#castbar .controls .control:empty{display:none}#castbar .controls .control .fa{width:50px;height:45px;line-height:45px;text-align:center}#castbar .controls .control-text{font-size:14px;height:45px;line-height:45px;margin-right:10px;overflow:hidden}body{margin-bottom:100px}
