.volume-slider{position:relative;width:100%;height:125px;background-color:#2E2E2E}.volume-slider .scrubber{position:relative;left:50%;top:20%;width:4px;height:60%;margin-left:-2px;background-color:#565656;pointer-events:none}.volume-slider .scrubber-head{position:absolute;left:50%;margin:0 auto;margin-bottom:-10px;width:20px;height:20px;border-radius:50%;margin-left:-10px;background-color:#428bca;pointer-events:none}.volume-slider .scrubber-percentage{position:absolute;width:4px;bottom:0;background-color:#428bca;pointer-events:none}
