#search{position:relative;width:100%}#search input[type=text]{width:100%;background-color:#414141;color:#fff;border:1px solid #6C6C6C}#search input[type=text]:focus{outline-width:0 !important;outline:none !important;border:1px solid #6C6C6C}#search .suggest{font-size:1.1rem;border:1px solid #6C6C6C;border-top:none;background-color:#414141;position:absolute;top:100%;left:0;z-index:100;width:100%;max-height:400px;overflow:hidden;overflow-y:auto}#search .suggest:empty{display:none}#search .suggest .result{color:#ccc;cursor:pointer;border-bottom:1px solid #6C6C6C;padding:0.2rem;width:100%;overflow:hidden}#search .suggest .result:last-child{border:none}#search .suggest .result .img,#search .suggest .result .title{pointer-events:none;float:left}#search .suggest .result .img{width:68px;overflow:hidden}#search .suggest .result .title{color:#fff;margin-left:0.5rem}#search .suggest .selected{background-color:#6A6A6A}
